@import url(https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard-dynamic-subset.css);
a{color:#3eb489}*::selection{background-color:#9dddc6}body{word-break:keep-all}.MuiTypography-root{transition:all .25s ease-in-out;-o-transition:all .25s ease-in-out;-moz-transition:all .25s ease-in-out;-webkit-transition:all .25s ease-in-out}
#contact{display:flex;align-items:flex-end;justify-content:flex-end;margin-top:1rem}#contact a:hover{color:teal;cursor:pointer}#contact a{margin:0 .3rem;text-decoration:underline;color:#5e5e5e}
.detail-container ul{padding-inline-start:1.3rem;position:relative}.detail-container li::marker{color:#3eb489}
